import config from '@/config/config.js';
import data from '@/config/data.js';
import previewImage from '@/components/kxj-previewImage/kxj-previewImage.vue';
export default {
	components:{previewImage},
	data() {
		return {
			baseUrl:config,
			//基本信息
			info:data.info,
			//生活照
			lifePhotoList:data.lifePhotoList,
			//资格证照片
			certificationPhotoList:data.certificationPhotoList,
			//月子餐照片
			mealPhotoList:data.mealPhotoList,
			//工作经历
			workExperience:data.workExperience,
			//预览图片数组
			imgList:[],
			//是否预览旋转
			circular:false
		}
	},
	onLoad() {
		wx.showShareMenu({
		  withShareTicket: true
		})
	},
	methods: {
		//图片预览
		imgPreview(imgList,index){
			if(imgList == 'icon'){
				this.imgList = [this.info.iconSrc]
				index = 0;
				this.circular = false;
			}else{
				this.imgList = imgList;
				this.circular = true;
			}
			this.$refs.previewImage.open(index);
			// imgList.forEach((item,index)=>{
			// 	imgList[index] = this.baseUrl+item;
			// })
			// uni.previewImage({
			// 	current:index,
			// 	urls:imgList,
			// 	indicator:'number'
			// })
		}
	}
}
